/* styles.css */

body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
}

nav {
  background-color: #ff0000;
  color: #fff;
  padding: 10px;
  display: flex;
  justify-content: space-between;
}

nav h1 {
  margin: 0;
}

nav ul {
  list-style-type: none;
  display: flex;
  padding: 0;
  margin: 0;
}

nav li {
  margin-right: 10px;
}

nav a {
  color: #fff;
  text-decoration: none;
}

.container {
  max-width: 600px;
  margin: 20px auto;
  padding: 20px;
  border: 1px solid #ccc;
  border-radius: 5px;
}

form {
  margin-bottom: 20px;
}

form input {
  margin-bottom: 10px;
  padding: 8px;
  width: 100%;
  box-sizing: border-box;
}

form button {
  padding: 8px 20px;
  background-color: #ff3300;
  color: #fff;
  border: none;
  cursor: pointer;
}
